//설정
@mixin flex($x: center, $y: center){
    display: flex;
    justify-content: $x; align-items: $y;
    @content;
}//@mixin flex($x: center, $y: center)

@mixin font($a: inherit, $b: inherit, $c:inherit){
    font-size: $a;
    font-weight: $b;
    color: $c;
    @content;
}//@mixin font($a: inherit, $b: inherit, $c:inherit)

//= = = = = = = = = = = = = = = = = = = = = = = = = 
//초기화
*{
    box-sizing: border-box;
    padding: 0; margin: 0;
    list-style-type: none;
}

a{
    text-decoration: none;
}

button{
    cursor: pointer;
}
//= = = = = = = = = = = = = = = = = = = = = = = = = 
//작업
div#root{

}

header{
    // outline: 1px solid black;
    position: fixed; top: 0; left: 0; z-index: 99;
    width: 100%;
    padding: 15px;

    @include flex(space-between);
    // background-color: black;
    
    >a.Logo{
        >h1{
            display: inline-block;
            @include font(2rem, 700,white){
                white-space: nowrap;
            };
        }
    }
    >nav{
        // outline: 1px solid red;
        display: inline-block;
        >a{
            position: relative;
            @include font(inherit, 700,white){
                white-space: nowrap;
            };

            text-align: center;
            margin: 0 10px;

            transition: .2s;

            &:before{
                content: '';
                position: absolute; left: 50%; bottom: -3px;
                display: inline-block;
                background-color: red;
                width: 0%; height: 1px;

                transition: .3s;
            }
            &:hover{
                color: red;
                &:before{
                    left: -5%;
                    width: 110%;
                }
            }
        }

    }//nav
}//header

main{
    height: 2000px;
    background-color: black;

    >section.popularMovie{
        outline: 1px solid black;
        width: 100%; height: 450px;
        >ul{
            width: 100%; height: 100%;
            @include flex(flex-start);
            >li{ 
                border: 1px solid red;
                width: 100%; height: 100%;
                flex-shrink: 0;

                padding: 100px 30px 0;
                
                >h2.title{
                    @include font(3.5rem, bold, white);
                }
                >p{
                    margin: 30px 0;
                    @include font(1rem, inherit, white);
                }
                >div.btnWrapper{
                    >button{
                        @include font(1rem, inherit, white);
                        border-radius: 12px;

                        padding: 7px 20px;
                        margin-right: 10px;
                        transition: .3s;

                        
                        &:nth-of-type(1){
                            border: 2px solid red; 
                            background-color: red;
                            box-shadow: 0 0 7px 8px rgba(255,0,0,.3);
                            &:hover{
                                box-shadow: 0 0 7px 15px rgba(255,0,0,.3);
                            }
                        }
                        &:nth-of-type(2){
                            border: 2px solid white; 
                            background-color: transparent;
                            
                            &:hover{
                                background-color: white;
                                color: red;
                            }
                        }
                    }//button
                }
            }//li
        }//ul
    }//section.popularMovie

    >section.MovieList{
        >article{
            // outline: 1px solid red;
            padding: 20px 10px;
            height: 370px;
            >div.header{
                // outline: 1px solid white;
                @include flex(space-between);
                padding-bottom: 20px;
                >h3{
                    @include font(1rem, bold, white);
                }
                
                >button{
                    @include font(.7rem, inherit, white);
                    background-color: transparent;
                    border: 1px solid white; 
                    border-radius: 12px;

                    padding: 3px 10px;
                    margin-right: 10px;
                    transition: .3s;

                    &:hover{
                        background-color: white;
                        color: red;
                    }
                }
            }//div.header

            >.thumList{
                // border: 1px solid white; 
                height: 52vw;
                >.mySwiper{
                    // border: 1px solid white; 
                    @include flex(flex-start);
                    height: 100%;
                    cursor: pointer;

                    >.swiper-wrapper{
                        flex-shrink: 0;
                        width: 20%; height: 100%;
                        >.SwiperSlide{
                            // border: 1px solid red; 
                            width: 100%; height: 100%;
                            >figure{
                                width: 100%; height: 100%;
                                >p{
                                    // border: 1px solid white;
                                    width: 100%;  height: 80%;
                                    border-radius: 10px;
                                    overflow: hidden;

                                    >img{
                                        border: none;
                                        width: 100%; height: 100%;
                                    }
                                }

                                >figcaption{
                                    margin-top: 10px; 
                                    @include font(.9rem, bold, white);
                                }
                            }//figure
                        }//li
                    }
                }//ul
            }//div.thumList
        }//article
    }//section.MovieList





    >.movieNTv{
        padding: 100px 30px 0;
        >h2{
            text-align: center;
            @include font(1.2rem, 700,white){
                white-space: nowrap;
            };
        }//h2

        >section.search{
            margin-top: 40px;

            >.searchInputWrapper{
                position: relative; top: 0; left: 0;
                display: inline-block;

                width: 140px;
                margin-right: 15px;

                >input[type='text']{
                    width: 100%;

                    color: gray;
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid gray;
                    padding: 5px;

                    transition: .3s;

                    &:focus{
                        outline: none;

                        & ~ .InputfocusLine{
                            width: 100%;
                        }
                    }

                }//input[type='text']

                >.InputfocusLine{
                    position: absolute; bottom: 0; left: 0;

                    display: inline-block;
                    width: 0%; height: 1px;
                    background-color: red;

                    transition: .3s;
                }//.InputfocusLine
            }//.searchInputWrapper

            >button{
                @include font(.8rem, inherit, white);
                border-radius: 9px;

                padding: 2px 12px;
                margin-right: 10px;
                border: 2px solid red;
                background-color: red;
                box-shadow: 0 0 7px 6px rgba(255,0,0,.3);
                
                transition: .3s;
                
                &:hover{
                    box-shadow: 0 0 7px 10px rgba(255,0,0,.3);
                }
            }//button
        }//section.search

        >section.thumList{
            margin-top: 20px;
            >ul{
                outline: 1px solid white;
                @include flex(flex-start){flex-wrap: wrap};
                >li{
                    outline: 1px solid white;
                    width: 20%;
                    flex-shrink: 0;
                    >figure{
                        width: 100%; height: 200px;
                        >p{
                            outline: 1px solid white;
                            width: 100%; height: 80%;
                            border-radius: 10px;
                            overflow: hidden;

                            >img{
                                border: none;
                                width: 100%; height: 100%;
                            }
                        }

                        >figcaption{
                            margin-top: 10px; 
                            @include font(.9rem, bold, white);
                        }
                    }//figure
                }//li
            }//ul
        }//section.thumList
    }//.movieNTv
}//main
